.resume {
    .info-section{
        width: 20%;
    }


    .content-area{
        margin-left: 20%;
        width: 80%;
        min-width: 300px;
        background: white;
        height: 100%;
        overflow: hidden;

        h1{
            font-weight: 700;
            text-transform: uppercase;
            font-size: 2.3rem;
            margin-bottom: 56px;
        }

       

        .ed-section{
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-column-gap: 56px;
            grid-row-gap: 30px;
            max-width: 1000px;
            
            .ed-box{
                padding: 30px;
                transition: all 0.5s ease-in-out;
                &:hover{
                -webkit-box-shadow: 0px 20px 60px 6px rgba(0, 0, 0, 0.15);
                -moz-box-shadow: 0px 20px 60px 6px rgba(0, 0, 0, 0.15);
                box-shadow: 0px 20px 60px 6px rgba(0, 0, 0, 0.15);
                }
                
                h5{
                font-weight: 500;
                text-transform: uppercase;
                font-size: 1.3rem;
                padding-bottom: 10px;
            }
            p{
            font-weight: 300;
            font-size: 1rem;
            margin-bottom: 1rem;
            }
            }
        }
    }
}